import Vue from 'vue'
import Router from 'vue-router'
import Home from './views/Home.vue'
import Inscription from './views/Inscription.vue'
import Question from './views/Questionnaire.vue'
import Resultat from './views/Resultat.vue'

Vue.use(Router)

export default new Router({
  routes: [
    {
      path: '/',
      name: 'inscription',
      component: Inscription
    },
    {
      path: '/accueil',
      name: 'accueil',
      component: Home
    },
    {
      path: '/questionnaire',
      name: 'questions',
      component: Question
    },
    {
      path: '/resultat',
      name: 'resultat',
      component: Resultat
    }
  ]
})
