<template>
  <div id="question" class="question">
    <!-- Utilisation du component question-->
    <question :jsonQuestion="jsonQuestions[index]" @reponseQuestionnaire="reponsesUtilisateur">
    </question>
  </div>
</template>

<script>
// @ is an alias to /src
import question from '@/components/Questions.vue'
import json from '../json/questions.json'

export default {
  name: 'questions',
  //Component utilisé pour la vue
  components: {
    question
  },
  data: () => ({
    nbPoints: 0,
    index: 0,
    //Liste des questions
    jsonQuestions: json,
    //Tableau regroupant les réponses de l'utilisateur
    tabReponseUtilisateur: []
  }),
  methods: {
    //Fonction permettant de passer à la question suivante
    suivante: function () {
      var long = this.$route.query.nbQuestions
      if (this.index < long - 1) {
        this.index++
      } else {
        this.$router.push({ name: 'resultat', query: { totalPoints: this.nbPoints, nbQuestions: this.$route.query.nbQuestions, tabReponseUtilisateur: this.tabReponseUtilisateur } })
      }
    },
    //Fonction récupérant les réponses saisies
    reponsesUtilisateur: function (reponse){
      if (reponse != null) {
        if (JSON.stringify(reponse) === JSON.stringify(this.jsonQuestions[this.index].reponses_utilisateur)) {
          this.nbPoints++
        }
        this.tabReponseUtilisateur[this.index] = this.jsonQuestions[this.index]
        this.suivante()
      }
    }
  }
}
</script>
