<template>
  <div class="home">
    <h1>Bienvenue sur notre Questionnaire !</h1>
    <div>
      <h3>Nombre de questions</h3>
      <input v-model="nbQuestions" type="range" min="1" max="5" value="5" class="slider"/>
    </div>
    <el-button v-on:click="parametre" type="submit">Commencer le Questionnaire !</el-button>
  </div>
</template>

<script>
export default {
  name: 'home',
  data: () => ({
    nbQuestions: 3
  }),
  methods: {
    parametre(){
      this.$router.push({ name: 'questions', query: {nbQuestions: this.nbQuestions}})
    }
  }
}
</script>
